.btn {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 24px;
  gap: 8px;
  border-radius: 0.25rem;
  border: none;
  letter-spacing: 0.5px;
  transition-duration: 0.5s;
  cursor: pointer;
  text-decoration: none;
}
.btn--primary {
  background-color: #171c60;
  border: 2px solid #171c60;
  color: #fff;
}
.btn--primary:hover {
  background-color: #1e247d;
}
.btn--primary.btn--disabled, .btn--primary:disabled {
  border: 2px solid #9FC1EF;
  background-color: #9FC1EF;
}
.btn--primary.btn--outline {
  background-color: transparent;
  color: #171c60;
}
.btn--primary.btn--outline:hover {
  background-color: #1e247d;
  color: #fff;
}
.btn--primary.btn--outline.btn--disabled, .btn--primary.btn--outline:disabled {
  border: 2px solid #9FC1EF;
  color: #9FC1EF;
}
.btn--primary.btn--outline.btn--disabled:hover, .btn--primary.btn--outline:disabled:hover {
  background-color: #9FC1EF;
  color: #fff;
}
.btn--secondary {
  background-color: #ff7551;
  border: 2px solid #ff7551;
  color: #fff;
}
.btn--secondary:hover {
  background-color: #ff9175;
}
.btn--secondary.btn--outline {
  background-color: transparent;
  color: #ff7551;
}
.btn--secondary.btn--outline:hover {
  background-color: #ff9175;
  color: #fff;
}
.btn--secondary.btn--disabled, .btn--secondary:disabled {
  border: 2px solid #ff9175;
  background-color: #ff9175;
  border: 2px solid #ff9175;
}

img {
  max-width: 100%;
  height: auto;
